: nowrap;\n}\n\n.sidebar-active .profile .profile_details {\n    opacity: 1;\n    pointer-events: auto;\n}\n\n.profile .profile_details img {\n    height: 45px;\n    width: 45px;\n    object-fit: cover;\n    border-radius: 12px;\n}\n\n.profile .profile_details .name_job {\n    font-size: 15px;\n    font-weight: 400;\n}\n\n.profile .profile_details .job {\n    font-size: 12px;\n}\n\n.profile #log_out {\n    position: absolute;\n    color: #fff;\n    left: 50%;\n    bottom: 5px;\n    transform: translateX(-50%);\n    min-width: 50px;\n    line-height: 50px;\n    font-size: 20px;\n    text-align: center;\n    border-radius: 12px;\n    transition: all 0.4s ease;\n}\n\n.profile #log_out:hover {\n    color: #11101d;\n    background: #fff;\n}\n\n.sidebar-active .profile #log_out {\n    left: 50%;\n}\n\n.sidebar-active ~ .equipments {\n    width: calc(100% - 300px);\n    left: 300px;\n}\n\n.sidebar-active ~ .pageHeader {\n    width: calc(100% - 300px);\n    left: 300px;\n}\n/* -- // SIDEBAR -- */\n\n/* PAGEHEADER */\n.pageHeader {\n    position: relative;\n    width: calc(100% - 78px);\n    left: 78px;\n    padding: 20px;\n    min-height: 100px;\n    margin-top: 10px;\n    transition: all 0.4s ease;\n}\n\n.pageHeader h2 {\n    font-size: 30px;\n}\n\n.pageOrder {\n    position: relative;\n    display: flex;\n    align-items: center;\n}\n\n.pageOrder i {\n    font-size: 22px;\n}\n\n.pageOrder a {\n    color: #000;\n    text-decoration: none;\n    float: right;\n}\n\n.pageSearch {\n    display: flex;\n    position: relative;\n    width: calc(100% - 78px);\n    left: 78px;\n    padding: 20px;\n    transition: all 0.4s ease;\n}\n\n.pageSearch form {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n}\n\n.pageSearch form a {\n    padding-left: 20px;\n}\n\n.pageSearch form .btn-success {\n    height: 50px;\n    margin-left: 15px;\n}\n\n.pageSearch form .btn-success p {\n    align-items: center;\n    margin: 0;\n}\n\n.pageSearch form .filterAndSeach {\n    margin-left: 15px;\n}\n\n.pageSearch form .filterAndSeach button {\n    background: #1d1b31;\n    color: #fff;\n    font-size: 18px;\n    height: 50px;\n    width: 150px;\n}\n\n.pageSearch form .filterAndSeach button p {\n    align-items: center;\n    margin: 0;\n}\n\n.pageSearch .equipmentSearch {\n    position: relative;\n    min-width: 500px;\n    height: 50px;\n    display: flex;\n    align-items: center;\n    background: #fff;\n    box-shadow: 0 7px 25px rgba(0, 0, 0, 0.2);\n    border-radius: 5px;\n    border: none;\n    z-index: 0;\n}\n\n.equipmentSearch .bx-search {\n    position: relative;\n    color: #11101d;\n    font-size: 22px;\n    transition: all 0.5s ease;\n}\n\n.equipmentSearch i {\n    min-width: 50px;\n    text-align: center;\n}\n\n.equipmentSearch input {\n    height: 100%;\n    width: 100%;\n    border: none;\n    outline: none;\n}\n/* // PAGEHEADER */\n\n\n/* EQUIPMENTS */\n.equipments {\n    position: relative;\n    width: calc(100% - 78px);\n    left: 78px;\n    padding: 20px;\n    margin-top: 10px;\n    display: grid;\n    transition: all 0.4s ease;\n    z-index: 0;\n}\n\n.equipments .recents {\n    position: relative;\n    display: grid;\n    padding: 20px;\n    background: #fff;\n    box-shadow: 0 7px 25px rgba(0, 0, 0, 0.2);\n    border-radius: 5px;\n}\n\n.equipments .recents table {\n    margin: 10px;\n}\n\n.cardHeader {\n    display: flex;\n    justify-content: space-between;\n    align-items: flex-start;\n}\n\n/* -- // EQUIPMENTS -- */\n\n.container-equipments {\n    max-width: 850px;\n    margin: 20px auto;\n    background: #fff;\n    padding: 20px;\n    border-radius: 10px;\n}\n\n.input-group {\n    width: 500px;\n    margin: 0 auto;\n    flex-wrap: nowrap;\n}\n\n.navbar {\n    padding: 10px;\n}\n.advanced {\n    text-decoration: none;\n    font-size: 15px;\n    font-weight: 500;\n}\n\n.btn-secondary,\n.btn-secondary:focus,\n.btn-secondary:active {\n    color: #fff;\n    background-color: #00838f !important;\n    