101d;\n    padding: 6px 14px;\n    transition: all 0.5s ease;\n    z-index: 99;\n}\n\n.sidebar-active {\n    width: 300px;\n}\n\n.sidebar .logo_content .logo {\n    color: #fff;\n    display: flex;\n    height: 50px;\n    width: 100%;\n    align-items: center;\n    opacity: 0;\n    pointer-events: none;\n}\n\n.sidebar-active .logo_content .logo {\n    opacity: 1;\n    transition: all 0.5s ease;\n}\n\n.logo_content .logo i {\n    font-size: 22px;\n    margin-right: 5px;\n}\n\n.logo_content .logo .logo_name {\n    font-size: 18px;\n    padding-left: 25px;\n    font-weight: 400;\n    white-space: nowrap;\n}\n\n.sidebar #btn {\n    position: absolute;\n    color: #fff;\n    left: 50%;\n    top: 6px;\n    font-size: 20px;\n    height: 50px;\n    width: 50px;\n    text-align: center;\n    line-height: 50px;\n    border-radius: 12px;\n    transform: translateX(-50%);\n    transition: all 0.5s ease;\n}\n\n.sidebar #btn:hover {\n    background: #fff;\n    color: #1d1b31;\n}\n\n.sidebar-active #btn {\n    left: 90%;\n}\n\n.sidebar ul {\n    margin-top: 20px;\n    padding-left: 0;\n}\n\n.sidebar ul li {\n    position: relative;\n    height: 50px;\n    width: 100%;\n    list-style: none;\n    line-height: 20px;\n    border-radius: 12px;\n}\n\n.sidebar .tooltip {\n    position: absolute;\n    left: 122px;\n    top: 0%;\n    transform: translateY(-50%, -50%);\n    border-radius: 6px;\n    height: 35px;\n    width: 170px;\n    background: #fff;\n    font-size: 15px;\n    line-height: 35px;\n    text-align: center;\n    white-space: nowrap;\n    box-shadow: 0 5px 10px rgba(0, 0, 0, 0.2);\n    transition: 0s;\n    opacity: 0;\n    pointer-events: none;\n    display: block;\n}\n\n.sidebar ul li:hover .tooltip {\n    transition: all 0.5s ease;\n    opacity: 1;\n    top: 50%;\n}\n\n.sidebar .profile_content .profile:hover .tooltip {\n    color: #11101d;\n    background: #fff;\n    transition: all 0.5s ease;\n    top: 10%;\n    opacity: 1;\n}\n\n\n.sidebar-active .tooltip {\n    display: none;\n}\n\n.sidebar ul li input {\n    position: absolute;\n    height: 100%;\n    width: 100%;\n    left: 0;\n    top: 0;\n    border-radius: 12px;\n    outline: none;\n    border: none;\n    background: #1d1b31;\n    padding-left: 50px;\n    font-size: 18px;\n    color: #fff;\n}\n\n.sidebar ul li .bx-search {\n    position: absolute;\n    color: #fff;\n    font-size: 22px;\n    transition: all 0.5s ease;\n    z-index: 99;\n}\n\n.sidebar ul li .bx-search:hover {\n    background: #fff;\n    color: #1d1b31;\n}\n\n.sidebar ul li a {\n    color: #fff;\n    display: flex;\n    align-items: center;\n    text-decoration: none;\n    transition: all 0.4s ease;\n    border-radius: 12px;\n    white-space: nowrap;\n}\n\n.sidebar ul li a:hover {\n    color: #11101d;\n    background: #fff;\n}\n\n.sidebar ul li i {\n    height: 50px;\n    min-width: 50px;\n    border-radius: 12px;\n    line-height: 50px;\n    text-align: center;\n}\n.sidebar .links_name {\n    opacity: 0;\n    pointer-events: none;\n}\n\n.sidebar-active .links_name {\n    opacity: 1;\n    pointer-events: auto;\n    transition: all 0.5s ease;\n}\n\n.sidebar .profile_content {\n    position: absolute;\n    color: #fff;\n    bottom: 0;\n    left: 0;\n    width: 100%;\n}\n\n.sidebar .profile_content .profile {\n    position: relative;\n    padding: 10px 6px;\n    height: 60px;\n    background: #1d1b31;\n}\n\n.sidebar .profile_content .profile .profile_details {\n    display: flex;\n    align-items: center;\n    opacity: 0;\n    pointer-events: none;\n    white-space: nowrap;\n}\n\n.sidebar-active .profile .profile_details {\n    opacity: 1;\n    pointer-events: auto;\n}\n\n.profile .profile_details img {\n    height: 45px;\n    width: 45px;\n    object-fit: cover;\n    border-radius: 12px;\n}\n\n.profile .profile_details .name_job {\n    font-size: 15px;\n    font-weight: 400;\n}\n\n.profile .profile_details .job {\n    font-size: 12px;\n}\n\n.profile #log_out {\n    position: absolute;\n    color: #fff;\n    left: 50%;\n    bottom: 5px;\n    transform: translateX(-50%);\n    min-width: 50px;\n    line-height: 50px;\n    font-size: 20px;\n    text-align: center;\n    border-radius: 12px;\n    transition: all 0.4s ease;\n}\n\n.profile #log_out:hover {\n    color: #11101d;\n    background: #fff;\n}\n\n.sidebar-active .profile #log_out {\n    left: 50%;\n}\n\n.sidebar-active ~ .equipments {\n    width: calc(100% - 300px);\n    left: 300px;\n}\n\n.sidebar-active ~ .pageHeader {\n    width: calc(100% - 300px);\n    left: 300px;\n}\n/* -- // SIDEBAR -- */\n\n/* PAGEHEADER */\n.pageHeader {\n    position: relative;\n    width: calc(100% - 78px);\n    left: 78px;\n    padding: 20px;\n    min-height: 100px;\n    margin-top: 10px;\n    transition: all 0.4s ease;\n}\n\n.pageHeader h2 {\n    font-size: 30px;\n}\n\n.pageOrder {\n    position: relative;\n    display: flex;\n    align-items: center;\n}\n\n.pageOrder i {\n    font-size: 22px;\n}\n\n.pageOrder a {\n    color: #000;\n    text-decoration: none;\n    float: right;\n}\n\n.pageSearch {\n    display: flex;\n    position: relative;\n    width: calc(100% - 78px);\n    left: 78px;\n    padding: 20px;\n    transition: all 0.4s ease;\n}\n\n.pageSearch form {\n    display: flex;\n    justify-content: center;\n    align-items: center;\n    width: 100%;\n}\n\n.pageSearch form a {\n    padding-left: 20px;\n}\n\n.pageSearch form .btn-success {\n    height: 50px;\n    margin-left: 15px;\n}\n\n.pageSearch form .btn-success p {\n    align-items: center;\n    margin: 0;\n}\n\n.pageSearch form .filterAndSeach {\n    margin-left: 15px;\n}\n\n.pageSearch form .filterAndSeach button {\n    background: #1d1b31;\n    color: #fff;\n    font-size: 18px;\n    height: 50px;\n    width: 150px;\n}\n\n.pageSearch form .filterAndSeach button p {\n    align-items: center;\n    margin: 0;\n}\n\n.pageSearch .equipmentSearch {\n    position: relative;\n    min-width: 500px;\n    height: 50px;\n    display: flex;\n    align-items: center;\n    background: #fff;\n    box-shadow: 0 7px 25px rgba(0, 0, 0, 0.2);\n    border-radius: 5px;\n    border: none;\n    z-index: 0;\n}\n\n.equipmentSearch .bx-search {\n    position: relative;\n    color: #11101d;\n    font-size: 22px;\n    transition: all 0.5s ease;\n}\n\n.equipmentSearch i {\n    min-width: 50px;\n    text-align: center;\n}\n\n.equipmentSearch input {\n    height: 100%;\n    width: 100%;\n    border: none;\n    outline: none;\n}\n/* // PAGEHEADER */\n\n\n/* EQUIPMENTS */\n.equipments {\n    position: relative;\n    width: calc(100% - 78px);\n    left: 78px;\n    padding: 20px;\n    margin-top: 10px;\n    display: grid;\n    transition: all 0.4s ease;\n    z-index: 0;\n}\n\n.equipments .recents {\n    position: relative;\n    display: grid;\n    padding: 20px;\n    background: #fff;\n    box-shadow: 0 7px 25px rgba(0, 0, 0, 0.2);\n    border-radius: 5px;\n}\n\n.equipments .recents table {\n    margin: 10px;\n}\n\n.cardHeader {\n    display: flex;\n    justify-content: space-between;\n    align-items: flex-start;\n}\n\n/* -- // EQUIPMENTS -- */\n\n.container-equipments {\n    max-width: 850px;\n    margin: 20px auto;\n    background: #fff;\n    padding: 20px;\n    border-radius: 10px;\n}\n\n.input-group {\n    width: 500px;\n    margin: 0 auto;\n    flex-wrap: nowrap;\n}\n\n.navbar {\n    padding: 10px;\n}\n.advanced {\n    text-decoration: none;\n    font-size: 15px;\n    font-weight: 500;\n}\n\n.btn-secondary,\n.btn-secondary:focus,\n.btn-secondary:active {\n    color: #fff;\n    background-color: #00838f !important;\n    border-color: #00838f !important;\n    box-shadow: none;\n}\n\n.advanced {\n    color: #00838f !important;\n}\n\n.form-control:focus {\n    box-shadow: none;\n    border: 1px solid #00838f;\n}\n\n@keyframes fadein {\n    from {\n        opacity:0;\n    }\n    to {\n        opacity:1;\n    }\n}\n@-moz-keyframes fadein { /* Firefox */\n    from {\n        opacity:0;\n    }\n    to {\n        opacity:1;\n    }\n}\n@-webkit-keyframes fadein { /* Safari and Chrome */\n    from {\n        opacity:0;\n    }\n    to {\n        opacity:1;\n    }\n}\n@-o-keyframes fadein { /* Opera */\n    from {\n        opacity:0;\n    }\n    to {\n        opacity: 1;\n    }\n}\n\n/* Login Container */\n.login-container {\n    width: 100%;\n    min-height: 100vh;\n    padding: 15px;\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    animation: fadein 1s;\n    -moz-animation: fadein 1s; /* Firefox */\n    -webkit-animation: fadein 1s; /* Safari and Chrome */\n    -o-animation: fadein 1s; /* Opera */\n}\n\n.login-container h1 {\n    font-size: 30px;\n}\n\n.login-container a {\n    text-decoration: none;\n}\n\n.form-signin .btn {\n    width: 100%;\n}\n/* -- Login Container -- */\n\n/* Login Form */\n.login-form {\n    width: 100%;\n    padding: 15px;\n}\n\n.login-form input {\n    width: 100%;\n    background-color: #e6e6e6;\n    border: 1px solid transparent;\n    border-radius: 3px;\n    margin-bottom: 16px;\n    line-height: 1.2;\n    font-size: 18px;\n    display: block;\n    height: 62px;\n    padding: 0 20px 0 38px;\n}\n\n.login-form button {\n    background-color: var(--primary-color);\n    color: #e6e6e6;\n    font-weight: 700;\n}\n\n.login-form button:hover {\n    color: #e6e6e6;\n}\n\n.login-form .messages-alert {\n    width: auto;\n    margin-bottom: 16px;\n}\n/* -- Login Form -- */\n\n/* Page 404 */\n.pageNotFound {\n    width: 100%;\n    min-height: 100vh;\n    display: -webkit-box;\n    display: -webkit-flex;\n    display: -moz-box;\n    display: -ms-flexbox;\n    display: flex;\n    flex-wrap: wrap;\n    justify-content: center;\n    padding: 10em;\n    animation: fadein 1s;\n    -moz-animation: fadein 1s; /* Firefox */\n    -webkit-animation: fadein 1s; /* Safari and Chrome */\n    -o-animation: fadein 1s; /* Opera */\n}\n\n.pageNotFound h1 {\n    font-family: \"Titillium Web\", sans-serif;\n    font-size: 186px;\n    font-weight: 900;\n    margin: 0px;\n    text-transform: uppercase;\n    background